import '../i18n/i18n';

import React from "react"
import Navigation from "../components/layout/Navigation";
import WeAreBlock from "../components/layout/WeAre";
import {SEO} from "../components/seo";

export default function AboutUs() {
    return (
        <main className="main-wrapper relative">
            <Navigation pageNr={'A0K1M'}/>
            <section className="pb-16">
                <WeAreBlock/>
            </section>
        </main>
    )
}

export const Head = () => <SEO title={"About Us"} description={'Learn more about the software development company KALMARS, as well as our key partners and areas of expertise such as IoT, Mobile applications and more.'} keywords={'Iot, best software development companies, web development'}/>
